<script>
import useVuelidate from "@vuelidate/core";
import { required, email, minLength, requiredIf } from "@vuelidate/validators";

export default {
  name: "SignUp",
  setup() {
    return { v$: useVuelidate() };
  },

  data() {
    return {
      loader: false,
      options: {
        centeredSlides: false,
        loop: false,
        slidesPerView: 1,
        autoplay: {
          delay: 3000,
        },
        spaceBetween: 15,
        pagination: {
          el: ".swiper-pagination",
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },

        // And if we need scrollbar
        scrollbar: {
          el: ".swiper-scrollbar",
        },
      },
      form: {
        name: "",
        password: "",
        email: "",
        fb_name: ''
      },
    
      regError: '',
    };
  },
  methods: {
   
    async onSubmit() {
      this.v$.$touch();

      if (!this.v$.form.$invalid) {
        this.regError = '';
        this.loader = true;
        try {
          await this.$store
            .dispatch("register", {
              email: this.form.email,
              password: this.form.password,
              name: this.form.name,
              fb_name: this.form.fb_name

            })
          this.loader = false;

          this.$router.push({ name: 'social.book' })

        }
        catch (error) {
          this.regError =error.response;
          this.loader = false;
        
          if (error.response.data.data == 'User already exist') {
            this.regError = ' البريد الالكتروني موجود مسبقاً، قم بتسجيل الدخول';
          }
          if (error.response.status == 413) {
            this.regError = ' الصورة حجمها كبير، اختر صورة أخرى';
          }
        }
      }
    },
  },
  validations() {
    return {
      form: {
        email: {
          required,
          email,
        },
        password: {
          required,
          min: minLength(8),
        },
        name: {
          required,
        },
        fb_name: {
          required
        },
       
      },
    };
  },
};
</script>

